import React, { Component } from 'react'
import { connect } from "react-redux";
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import { Form, Button, Badge } from 'react-bootstrap';
import { removeFilterSocialFeeds, removeFilterPostType, removeFilterPostTags, removeDateRanges, removeAllFilterBody, removeFilterRecommendation, removeFilterSentiment, removeFilterLanguage, removeFilterUgc, removeRuleFilter } from '../../../../../actions/moderationActions'
import HtmlParser from 'react-html-parser';

const FilterOption = ({ text, filterAction }) => {
    return <Badge bg='gray-300' className="rounded-pill fil_network badge bg-gray-300"> {HtmlParser(text)}
        <Button variant='link' className='border-0 ms-2 btn btn-link' onClick={filterAction}>
            <FontAwesomeIcon type={'solid'} icon={'xmark'} iconClass="fs-6" />
        </Button>
    </Badge>
}
class FilterTab extends Component {
    onSelectFeeds = (data) => event => { this.props.removeFilterSocialFeeds(data) }
    onClickPostType = (value) => event => { this.props.removeFilterPostType(value) }
    onClickRemovePostTags = (value, isText) => event => { this.props.removeFilterPostTags(value, isText) }
    onClickToDateRangeRemove = event => { this.props.removeDateRanges() }
    onClickRecommend = (value) => event => { this.props.removeFilterRecommendation(value) }
    onClickSentiment = (value) => event => { this.props.removeFilterSentiment(value) }
    onClicklanguage = (value) => event => { this.props.removeFilterLanguage(value) } 

    render() {
        const { filters, onClickReset, filterBody } = this.props
        return (filterBody?.socialFeed?.length > 0 || filterBody?.postType?.length > 0 || filterBody?.postTags?.length > 0 || filterBody?.other?.length > 0 || (filterBody?.ugc && filterBody?.ugc?.length > 0) || filterBody?.language?.length > 0 || filterBody?.rules?.length > 0 || Object.keys(filterBody?.dateRanges)?.length > 0 || Object.keys(filterBody?.recommendation)?.length > 0 || Object.keys(filterBody?.sentiment)?.length > 0 || filters?.text?.length > 0 || filters?.rulename?.length > 0)  ? <div className="d-flex align-items-start mb-2">
            <span className="text-secondary fw-medium me-2 my-2 text-nowrap">Filter By : </span>
            <div className="d-flex flex-wrap align-items-center">

                {(Object.keys(filterBody?.dateRanges)?.length > 0) ? <FilterOption text={`${filterBody?.dateRanges.startDate} - ${filterBody?.dateRanges.endDate}`} filterAction={this.onClickToDateRangeRemove} /> : null}
                {filterBody?.postTags && filterBody?.postTags.map((sItem) => <FilterOption text={sItem} filterAction={this.onClickRemovePostTags(sItem  , false)} />)}
                {filters?.text?.length > 0 ? <FilterOption text={filters?.text} filterAction={this.onClickRemovePostTags(filters?.text, true)} /> : null}
                {filterBody?.socialFeed && filterBody?.socialFeed.map((sItem) => <FilterOption text={sItem.key =="UGC Uploader" ? "SnapUp" : sItem.key} filterAction={this.onSelectFeeds(sItem)} />)}

                {filterBody?.postType && filterBody?.postType.map((sItem) => <FilterOption text={sItem == "1" ? 'Text Only' : sItem == "2,4" ? 'With Image' : sItem == "3,5" ? 'With Video' : ''} filterAction={this.onClickPostType(sItem)} />)}
                {filterBody?.recommendation && filterBody?.recommendation.map((sItem) => <FilterOption text={sItem == "1" ? 'Recommended' : sItem == "2" ? 'Considerable' : sItem == "3" ? 'Not-Recommended' : ''} filterAction={this.onClickRecommend(sItem)} />)}
                {filterBody?.sentiment && filterBody?.sentiment.map((sItem) => <FilterOption text={sItem == "1" ? 'Strongly Positive' : sItem == "2" ? 'Positive' : sItem == "3" ? 'Neutral' : sItem == "4" ? 'Negative' : sItem == "5" ? 'Strongly Negative' : ''} filterAction={this.onClickSentiment(sItem)} />)}

                {filterBody?.language && filterBody?.language.map((sItem) => <FilterOption text={sItem} filterAction={this.onClicklanguage(sItem)} />)}

                {filterBody?.rules?.length > 0 && filterBody?.rules.map((sItem) => <FilterOption text={sItem} filterAction={() => this.props.removeRules(sItem, true)} />)}
                {filters?.rulename?.length > 0 ? <FilterOption text={filters?.rulename} filterAction={() => this.props.removeRules(filters?.rulename, false)} /> : null}

                {filterBody?.ugc && filterBody?.ugc.map((sItem) => <FilterOption text={sItem.key == "moderationpending" ? 'Pending Post' : sItem.key == "moderationapproved" ? 'Approved Post' : sItem.key == "moderationrejected" ? 'Rejected Post' : sItem.key == "moderationexpired" ? 'Expired Post' : ''} filterAction={this.onClickUgc(sItem)} />)}

                <Button variant='dark' className='rounded-pill py-1 m-1' onClick={this.props.removeAllFilterBody}>Reset</Button>
            </div>
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return { moderation: state.moderation }
}
const mapDispatchToProps = dispatch => {
    return {
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterPostTags: (data, isText) => dispatch(removeFilterPostTags(data, isText)),
        removeDateRanges: () => dispatch(removeDateRanges()),
        removeFilterUgc: (data) => dispatch(removeFilterUgc(data)),
        removeFilterRecommendation: (data) => dispatch(removeFilterRecommendation(data)),
        removeFilterSentiment: (data) => dispatch(removeFilterSentiment(data)),
        removeRules: (data, type) => dispatch(removeRuleFilter(data, type)),
        removeFilterLanguage: (data) => dispatch(removeFilterLanguage(data)),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterTab);